import React from "react";
import hollerIcon from '../../icons/hollerIcon.png';

const Login = (props) => {
  const {
    email,
    setEmail,
    password,
    setPassword,
    name,
    setName,
    room,
    setRoom,
    handleLogin,
    handleSignup,
    hasAccount,
    setHasAccount,
    emailError,
    passwordError,
    roomError,
   } = props;

  return (
    <React.Fragment>
      <section className="login">
        
        <div className="loginContainer">
        <div className="logo"><img src={hollerIcon} alt="online icon" /></div>
          <label>Username</label>
          <input
            type="text"
            autoFocus
            required
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <p className="errorMsg">{emailError}</p>
          <label>Password</label>
          <input
            type="password"
            required
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <p className="errorMsg">{passwordError}</p>
          <div className="btnContainer">
            {hasAccount ? (
            <React.Fragment>
               <button className="loginBTN" onClick={handleLogin}>Sign In</button>
              <p>
                Don't have an account yet? Com'on,{" "}
                <span
                  onClick={() => {
                    setHasAccount(!hasAccount);
                  }}
                >
                  Sign Up!
                </span>
              </p>
            </React.Fragment>
            ) : (
            <React.Fragment>
              <label>Name</label>
              <input
                type="text"
                autoFocus
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            <label>Location</label>
              <input
                type="text"
                autoFocus
                required
                value={room}
                onChange={(e) => setRoom(e.target.value)}
              />
              <p className="errorMsg">{roomError}</p>
              <button className="loginBTN" onClick={handleSignup}>Sign Up</button>
              <p>
                Already have an account ?{" "}
                <span
                  onClick={() => {
                    setHasAccount(!hasAccount);
                  }}
                >
                  Sign In
                </span>
              </p>
            </React.Fragment>
            )}
          </div>
        </div>
      </section>
    </React.Fragment>
  );
};

export default Login;
